import React, { Component } from 'react'
import{Link} from "react-router-dom"
import { AiOutlineHome,AiOutlinePartition } from "react-icons/ai";
import { FiMenu } from "react-icons/fi";
import "./header.scss";
export default class Header extends Component {
    constructor(){
        super()
        this.state={
            MenuOpen:false
        }
    }
    render() {
        var _Menu=()=>{
            return(
                <div className="_Menu">
                    <Link to="/">
                        <div><AiOutlineHome/>Home</div>
                    </Link>
                    <Link to="/about">
                        <div>afsddaf</div>
                    </Link>
                </div>
            )
            
        }
        var OnClickMenu=()=>{
            this.setState({
                MenuOpen:true
            },()=>{
                document.body.style.overflow="hidden"
            })
        }
        var OnOutMenu=()=>{
            this.setState({
                rmvClass:"rmv"
            },()=>{
                setTimeout(()=>{
                    this.setState({
                        MenuOpen:false,
                        rmvClass:""
                    })
                    document.body.style.overflow="auto"
                },400)
                
            })
        }
        var MobMenu=()=>{
            return(
                <div onClick={OnOutMenu} className="_MobMenu">
                     <div className={this.state.rmvClass} onClick={(e)=>{e.stopPropagation()}}>
                        <div className="_head">
                            GULSHAN
                        </div>
                        <div className="_Cont">
                            {_Menu()}
                        </div>
                     </div>
                </div>
            )
        }
        return (
            <div className="_header">
                {this.state.MenuOpen&&<MobMenu/>}
                <div className="MenuBut">
                    <div>
                        <span onClick={OnClickMenu}>
                            <FiMenu/>
                        </span>
                        
                    </div>
                </div>
                <div className="_Name">
                    GULSHAN
                </div>
                
                <div>
                   {_Menu()}
                    
                </div>
                
            </div>
        )
    }
}
